import Mar31 from './panels/marophon/Mar31';




const App = () => {



	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(null);

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await connect.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);
   

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
		};

	

	return (
		<View activePanel={activePanel} popout={popout}>

			<Home id='home' fetchedUser={fetchedUser} go={go} />
			
			
			<Marophons id='marophons' go={go} />
			<Program id='program' go={go} />
			<Prog1 id='prog1' go={go} />
			<Prog2 id='prog2' go={go} />
			<Prog3 id='prog3' go={go} />
			<Prog4 id='prog4' go={go} />
			<Mar1 id='mar1' go={go} />
			<Mar2 id='mar2' go={go} />
			<Mar3 id='mar3' go={go} />
			<Mar4 id='mar4' go={go} />
			<Mar5 id='mar5' go={go} />
			<Mar6 id='mar6' go={go} />
			<Mar7 id='mar7' go={go} />
			<Mar8 id='mar8' go={go} />
			<Mar9 id='mar9' go={go} />
			<Mar10 id='mar10' go={go} />
			<Mar11 id='mar11' go={go} />
			<Mar12 id='mar12' go={go} />
			<Mar13 id='mar13' go={go} />
			<Mar14 id='mar14' go={go} />
			<Mar15 id='mar15' go={go} />
			<Mar16 id='mar16' go={go} />
			<Mar17 id='mar17' go={go} />
			<Mar18 id='mar18' go={go} />
			<Mar19 id='mar19' go={go} />
			<Mar20 id='mar20' go={go} />
			<Mar21 id='mar21' go={go} />
			<Mar22 id='mar22' go={go} />
			<Mar23 id='mar23' go={go} />
			<Mar24 id='mar24' go={go} />
			<Mar25 id='mar25' go={go} />
			<Mar26 id='mar26' go={go} />
			<Mar27 id='mar27' go={go} />
			<Mar28 id='mar28' go={go} />
			<Mar29 id='mar29' go={go} />
			<Mar30 id='mar30' go={go} />
			<Mar31 id='mar31' go={go} />

			
		</View>
	);
}

export default App;

